<template>
  <div class="msg-view-container">
    <!-- Sidebar: Users List -->
    <div class="sidebar">
      <!-- Dark Mode Toggle Button -->
<button class="theme-toggle" @click="toggleTheme" :title="isDarkMode ? 'Light Mode' : 'Dark Mode'">
  {{ isDarkMode ? 'üåô' : '‚òÄÔ∏è' }}
</button>

      <input v-model="search" @input="fetchUsers" placeholder="Search users..." class="search-bar" >
      
      <ul>
        <li v-for="user in filteredUsers" :key="user.id" @click="selectUser(user)" :class="{ active: selectedUser?.id === user.id }">
          <img :src="user.profile_picture || defaultProfile" class="avatar" />
          <span>{{ user.name }}</span>
        </li>
      </ul>
    </div>
    

    <!-- Chat Area -->
     
    <div class="chat-area" v-if="selectedUser">
      <div class="chat-header">
        <img :src="selectedUser.profile_picture || defaultProfile" class="avatar" />
        <h3>{{ selectedUser.name }}</h3>
        <div class="status">{{ typing ? `${selectedUser.name} is typing...` : '' }}</div>
      </div>

      <!-- Messages -->
      <div class="chat-messages" ref="chatMessages">
        <div v-for="msg in messages" :key="msg.id" class="message" :class="{ 'own': msg.sender_id === currentUser.id }">
          <div class="msg-content">
            <div v-if="msg.file">
              <a :href="msg.file" target="_blank">üìé File</a>
            </div>
            <div v-else>{{ msg.content }}</div>
            <span class="timestamp">{{ formatTime(msg.timestamp) }}</span>
          </div>
          <div class="reactions">
            <span v-for="(emoji, index) in msg.reactions" :key="index">{{ emoji }}</span>
          </div>
        </div>
      </div>

      <!-- Input -->
      <div class="chat-input">
        <textarea v-model="newMessage" @keyup.enter.exact.prevent="sendMessage" @input="emitTyping" placeholder="Type a message..."></textarea>
        <input type="file" ref="fileInput" @change="uploadFile" hidden />
        <button @click="triggerFileInput">üìé</button>
        <button @click="toggleEmoji">üòä</button>
        <button @click="sendMessage">Send</button>
      </div>

      <!-- Emoji Picker -->
      <div v-if="showEmoji" class="emoji-picker">
        <span v-for="emoji in emojis" :key="emoji" @click="addEmoji(emoji)">{{ emoji }}</span>
      </div>
    </div>

    <div v-else class="no-user">
      <p>Select a user to start chatting.</p>
    </div>
  </div>
</template>


<script setup>
import { ref, onMounted, reactive, nextTick, computed } from 'vue'
import { io } from 'socket.io-client'

const socket = io()

// State
const users = ref([])
const search = ref('')
const messages = ref([])
const newMessage = ref('')
const selectedUser = ref(null)
const showEmoji = ref(false)
const typing = ref(false)
const chatMessages = ref(null)

const currentUser = reactive({ id: 0, name: '' }) // Replace with actual logic to get from JWT

// Emojis
const emojis = ['üòÄ', 'üòÇ', 'üòç', 'üò¢', 'üò°', 'üëç', 'üëé', '‚ù§Ô∏è', 'üéâ']

// Fetch users
const fetchUsers = async () => {
  const res = await fetch('/chat/users', {
    headers: { Authorization: `Bearer ${localStorage.getItem('token')}` },
  })
  users.value = await res.json()
}

// Filtered users
const filteredUsers = computed(() =>
  users.value.filter((user) =>
    user.name.toLowerCase().includes(search.value.toLowerCase())
  )
)

// Select chat user and load messages
const selectUser = async (user) => {
  selectedUser.value = user
  const res = await fetch(`/chat/messages/${user.id}`, {
    headers: { Authorization: `Bearer ${localStorage.getItem('token')}` },
  })
  messages.value = await res.json()
  nextTick(() => scrollToBottom())
}

// Send message
const sendMessage = async () => {
  if (!newMessage.value.trim()) return
  const msg = {
    content: newMessage.value,
    receiver_id: selectedUser.value.id,
  }
  const res = await fetch('/chat/messages', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${localStorage.getItem('token')}`,
    },
    body: JSON.stringify(msg),
  })
  const savedMsg = await res.json()
  messages.value.push(savedMsg)
  newMessage.value = ''
  socket.emit('send_message', savedMsg)
  scrollToBottom()
}

// Typing indicator
const emitTyping = () => {
  socket.emit('typing', { to: selectedUser.value.id })
}

// Scroll helper
const scrollToBottom = () => {
  nextTick(() => {
    if (chatMessages.value) {
      chatMessages.value.scrollTop = chatMessages.value.scrollHeight
    }
  })
}

// Emoji logic
const addEmoji = (emoji) => {
  newMessage.value += emoji
  showEmoji.value = false
}
const toggleEmoji = () => {
  showEmoji.value = !showEmoji.value
}



const isDarkMode = ref(false);

const toggleTheme = () => {
  isDarkMode.value = !isDarkMode.value;
  document.documentElement.classList.toggle('dark-mode', isDarkMode.value);
  localStorage.setItem('theme', isDarkMode.value ? 'dark' : 'light');
};

onMounted(() => {
  const saved = localStorage.getItem('theme');
  if (saved === 'dark') {
    isDarkMode.value = true;
    document.documentElement.classList.add('dark-mode');
  }
});


// File upload
const uploadFile = async (e) => {
  const file = e.target.files[0]
  if (!file) return
  const formData = new FormData()
  formData.append('file', file)

  const res = await fetch('/chat/upload', {
    method: 'POST',
    headers: { Authorization: `Bearer ${localStorage.getItem('token')}` },
    body: formData,
  })

  const data = await res.json()
  const msg = {
    file: data.url,
    receiver_id: selectedUser.value.id,
  }

  const postRes = await fetch('/chat/messages', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: `Bearer ${localStorage.getItem('token')}`,
    },
    body: JSON.stringify(msg),
  })

  const savedMsg = await postRes.json()
  messages.value.push(savedMsg)
  socket.emit('send_message', savedMsg)
  scrollToBottom()
}

const triggerFileInput = () => {
  document.querySelector('input[type="file"]').click()
}

// Format time
const formatTime = (timestamp) => {
  return new Date(timestamp).toLocaleTimeString()
}

// Sockets
socket.on('receive_message', (msg) => {
  if (msg.sender_id === selectedUser.value?.id) {
    messages.value.push(msg)
    scrollToBottom()
  }
})

socket.on('user_typing', ({ from }) => {
  if (selectedUser.value && selectedUser.value.id === from) {
    typing.value = true
    setTimeout(() => (typing.value = false), 1500)
  }
})

// On component mount
onMounted(() => {
  fetchUsers()
})
</script>



<style scoped>
.msg-view-container {
  display: flex;
  height: 90vh;
  background-color: #d9f3d2;
}
.sidebar {
  width: 25%;
  border-right: 1px solid #010101;
  overflow-y: auto;
}
.search-bar {
  width: 100%;
  padding: 10px;
  border: none;
  border-bottom: 1px solid #ccc;
}
.sidebar ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.sidebar li {
  display: flex;
  align-items: center;
  padding: 10px;
  cursor: pointer;
}
.sidebar li.active {
  background-color: #e0e0e0;
}
.sidebar .avatar {
  width: 32px;
  height: 32px;
  margin-right: 10px;
  border-radius: 50%;
}
.chat-area {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.chat-header {
  display: flex;
  align-items: center;
  padding: 10px;
  background: #7ec392;
  border-bottom: 1px solid #ccc;
}
.chat-header .avatar {
  width: 36px;
  height: 36px;
  margin-right: 10px;
  border-radius: 50%;
}
.chat-messages {
  flex: 1;
  padding: 10px;
  overflow-y: auto;
  background: #e5ddd5;
}
.message {
  margin: 5px 0;
  max-width: 60%;
}
.message.own {
  margin-left: auto;
  background: #dcf8c6;
}
.message .msg-content {
  background: #fff;
  padding: 8px;
  border-radius: 8px;
  position: relative;
}
.timestamp {
  font-size: 0.7em;
  color: #999;
  margin-left: 5px;
}
.chat-input {
  display: flex;
  padding: 10px;
  background: #fff;
  align-items: center;
}
.chat-input textarea {
  flex: 1;
  resize: none;
  border: none;
  padding: 8px;
  border-radius: 6px;
}
.chat-input button {
  background: none;
  border: none;
  margin-left: 5px;
  font-size: 1.2em;
  cursor: pointer;
}
.emoji-picker {
  background: #fff;
  padding: 10px;
  border-top: 1px solid #ccc;
  display: flex;
  flex-wrap: wrap;
}
.emoji-picker span {
  font-size: 1.5em;
  margin: 5px;
  cursor: pointer;
}
.no-user {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #777;
}

.theme-toggle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #f4f4f4;
  border: 1px solid #ccc;
  font-size: 18px;
  cursor: pointer;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
}
.theme-toggle:hover {
  background-color: #ddd;
}

/* Dark mode global styles */
:global(.dark-mode) {
  background-color: #121212;
  color: #ffffff;
}

:global(.dark-mode input),
:global(.dark-mode textarea) {
  background-color: #2a2a2a;
  color: #fff;
  border-color: #444;
}

:global(.dark-mode .message-bubble) {
  background-color: #2e2e2e;
}

.theme-toggle {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #f4f4f4;
  border: 1px solid #ccc;
  font-size: 18px;
  cursor: pointer;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.3s ease;
}
.theme-toggle:hover {
  background-color: #ddd;
}

/* Global dark theme base */
:global(.dark-mode) {
  background-color: #121212;
  color: #f5f5f5;
}

/* Make sure inputs/textareas adapt */
:global(.dark-mode input),
:global(.dark-mode textarea) {
  background-color: #2a2a2a;
  color: #fff;
  border-color: #555;
}

/* Optional: override link styles */
:global(.dark-mode a) {
  color: #8ab4f8;
}

/* Chat area specific */
.chat-container {
  background-color: #ffffff;
  transition: background 0.3s ease, color 0.3s ease;
}
.chat-container.dark-chat {
  background-color: #1f1f1f;
  color: #e0e0e0;
}

/* Message bubbles */
.message-bubble {
  background-color: #e4e4e4;
  color: #251c1c84;
  padding: 8px 12px;
  border-radius: 8px;
  margin-bottom: 6px;
  max-width: 60%;
}
.dark-chat .message-bubble {
  background-color: #333;
  color: #fff;
}

</style>
