<template>
  <div class="post-page">
    <h2>üì¢ Share Your Ideas / Notes / Videos</h2>

    <!-- Post submission form -->
    <form @submit.prevent="submitPost" class="post-form">
      <input
        v-model="newPost.title"
        type="text"
        placeholder="Enter post title (e.g., My Notes on AI)"
        required
      />
      <textarea
        v-model="newPost.content"
        placeholder="Write your idea, note, or video link here..."
        required
      ></textarea>
      <button type="submit">Post</button>
    </form>

    <!-- Search bar -->
    <div class="search-bar">
      <input
        v-model="searchQuery"
        type="text"
        placeholder="üîç Search posts by title, content, or author"
      />
    </div>

    <!-- Posts feed -->
    <div class="post-feed">
      <h3>üìù Recent Posts</h3>
      <div
        class="post-card"
        v-for="post in filteredPosts"
        :key="post.id"
      >
        <div class="post-header">
          <strong>{{ post.author.name }}</strong>
          <span class="meta">{{ post.author.branch }}, Year {{ post.author.year }}</span>
        </div>
        <div class="post-content">
          <h4>{{ post.title }}</h4>
          <p v-if="isYouTubeLink(post.content)">
            üìπ Video Link: <a :href="post.content" target="_blank">{{ post.content }}</a>
          </p>
          <p v-else>{{ post.content }}</p>
        </div>
      </div>
      <div v-if="filteredPosts.length === 0">
        <p>No posts found matching your search.</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PostView',
  data() {
    return {
      currentUser: {
        id: 1,
        name: 'You',
        branch: 'CSE',
        year: 3
      },
      searchQuery: '',
      newPost: {
        title: '',
        content: ''
      },
      posts: []
    };
  },
  computed: {
    filteredPosts() {
      const q = this.searchQuery.toLowerCase();
      return this.posts.filter(post =>
        post.title.toLowerCase().includes(q) ||
        post.content.toLowerCase().includes(q) ||
        post.author.name.toLowerCase().includes(q)
      );
    }
  },
  methods: {
    async fetchPosts() {
      try {
        const response = await fetch('/posts'); // ‚úÖ Relative fetch instead of hardcoded backend
        if (!response.ok) throw new Error('Failed to fetch posts');
        const data = await response.json();
        this.posts = data;
      } catch (err) {
        console.error(err);
      }
    },
    async submitPost() {
      const newPostData = {
        title: this.newPost.title,
        content: this.newPost.content,
        author: this.currentUser
      };
      try {
        const response = await fetch('/posts', {
          method: 'POST', // ‚úÖ Relative POST address
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(newPostData)
        });
        if (!response.ok) throw new Error('Failed to submit post');
        const createdPost = await response.json();
        this.posts.unshift(createdPost); // Immediately reflect new post in UI
        this.newPost.title = '';
        this.newPost.content = '';
      } catch (err) {
        console.error(err);
      }
    },
    isYouTubeLink(content) {
      return content.includes('youtube.com') || content.includes('youtu.be');
    }
  },
  mounted() {
    this.fetchPosts(); // ‚úÖ Fetch on mount
  }
};
</script>

<style scoped>
.post-page {
  padding: 20px;
  max-width: 800px;
  margin: auto;
  font-family: Arial, sans-serif;
}
.post-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 30px;
}
.post-form input,
.post-form textarea {
  padding: 10px;
  font-size: 16px;
}
.post-form button {
  padding: 10px 15px;
  font-size: 16px;
  background-color: #2d88ff;
  color: white;
  border: none;
  cursor: pointer;
}
.search-bar {
  margin-bottom: 20px;
}
.search-bar input {
  padding: 8px 12px;
  width: 100%;
  font-size: 16px;
}
.post-feed {
  border-top: 1px solid #ccc;
  padding-top: 20px;
}
.post-card {
  background: #fafafa;
  border: 1px solid #ddd;
  margin-bottom: 20px;
  padding: 15px;
  border-radius: 8px;
}
.post-header {
  margin-bottom: 10px;
}
.meta {
  font-size: 12px;
  color: #555;
  margin-left: 10px;
}
</style>
