<template>
  <div class="p-6 max-w-3xl mx-auto">
    <h2 class="text-2xl font-semibold mb-4 text-center">üìÖ Upcoming Sessions</h2>

    <!-- Displaying dummy cards -->
    <div class="grid grid-cols-1 gap-4 mb-8">
      <GroupSessionCard v-for="i in 3" :key="i" />
    </div>

    <!-- Session Creation Form -->
    <div class="bg-white rounded-xl shadow-md p-6 border border-gray-200">
      <h3 class="text-xl font-bold mb-4">‚ûï Create New Session</h3>
      <form @submit.prevent="createSession" class="space-y-4">
        <input
          v-model="topic"
          placeholder="Enter session topic"
          required
          class="w-full p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <input
          v-model="time"
          type="datetime-local"
          required
          class="w-full p-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
          type="submit"
          class="w-full bg-blue-600 text-white py-2 px-4 rounded-lg hover:bg-blue-700 transition duration-200"
        >
          Create Session
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import GroupSessionCard from '@/components/GroupSessionCard.vue';

export default {
  components: { GroupSessionCard },
  data() {
    return {
      topic: '',
      time: ''
    };
  },
  methods: {
    async createSession() {
      try {
        const token = localStorage.getItem('token'); // Assuming JWT is stored in localStorage
        if (!token) {
          alert('‚ùå You must be logged in to create a session');
          return;
        }

        const response = await fetch('/session/create', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
            Authorization: `Bearer ${token}`
          },
          body: JSON.stringify({
            topic: this.topic,
            time: this.time
          })
        });

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || 'Unknown error');
        }

        const result = await response.json();
        alert(`‚úÖ Session created: "${result.topic}" at ${new Date(result.time).toLocaleString()}`);

        // Clear form fields
        this.topic = '';
        this.time = '';
      } catch (err) {
        alert(`‚ùå Error: ${err.message}`);
        console.error(err);
      }
    }
  }
};
</script>

